header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100;
    position: relative;
}


/* CTA */

.cta{
   margin-top: 2.5rem; 
   display: flex;
   gap: 1.2rem;
   justify-content: center;
   

}

/* HEADER SOCIALS */

.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
    
}

.header_socials:after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

        /* ME */

.headImg{
    width: 15rem;
    position:absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 4rem;
    border:10px solid rgb(0, 0, 0) ;
    border-radius:20% 5px;
    box-shadow: #4db5ff 6px 2px 16px 0px, rgba(43, 188, 255, 0.8) -6px -2px 16px 0px;
    
    
}


/* SCROLL DOWN */
.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom:5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* MEDIA QUERIES (MEDIUM DEVICES) */
@media screen and (max-width: 1024px){
.header{
    height: 68hv;
}
}

/* MEDIA QUERIES (SMALL DEVICES) */
@media screen and (max-width: 1024px){
.header{
    height:100hv;
}
.header_socials,.scroll_down{
    display: none;
}
}