@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
.about_container{
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
    
    
}


.about_me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius:2rem ;
    background:linear-gradient(45drg,transparent,
    var(--color-primary),transparent
    );
    display: grid;
    place-items: center;
}
.about_image{
    width: 50%;
    border-radius: 2rem;
    overflow:hidden;
    -webkit-transform:rotate(0deg);
            transform:rotate(0deg);
    transition: var(--transition);
}

.about_image:hover{
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: var(--transition);
}

.about_cards{
    display: grid;
    grid-template-columns: repeat( 3, 1fr);
    grid-gap:2rem;
    gap:2rem;
    
}

.about_card{
    background: var(--color-bg-variant);
    border: 1px solid transparent ;
    border-radius: 1rem;
    padding: 1rem;
    text-align: center;
    
}

.about_card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about_icon{
    color:rgb(152, 228, 228);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about_card h4{
    font-size: 0.95rem;
    color:var(--color-primary);
}

.about_card small{
    font-size: 0.7rem;
    color:var(--color-light)
}

.about_content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

/* MEDIA QUERIES (medium devices)*/

@media screen and (max-width: 1024px){
    .about_container{
       grid-template-columns: 1fr ; 
       grid-gap:0; 
       gap:0;
    }
    
    .about_content{
       margin: 1rem 0 1.5rem;
    }
}

/* MEDIA QUERIES (small devices)*/

@media screen and (max-width: 600px){

    .about_me{
        width: 65%;
        margin:0 auto 3rem;
    }
  .about_cards{
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
    gap: 1rem;
  }
  
  .about_content{
    text-align: center;
  }
   .about_content p{
    margin: 1.5rem  0;
   }
}


header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100;
    position: relative;
}


/* CTA */

.cta{
   margin-top: 2.5rem; 
   display: flex;
   grid-gap: 1.2rem;
   gap: 1.2rem;
   justify-content: center;
   

}

/* HEADER SOCIALS */

.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap:0.8rem;
    gap:0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
    
}

.header_socials:after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

        /* ME */

.headImg{
    width: 15rem;
    position:absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    margin-top: 4rem;
    border:10px solid rgb(0, 0, 0) ;
    border-radius:20% 5px;
    box-shadow: #4db5ff 6px 2px 16px 0px, rgba(43, 188, 255, 0.8) -6px -2px 16px 0px;
    
    
}


/* SCROLL DOWN */
.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom:5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* MEDIA QUERIES (MEDIUM DEVICES) */
@media screen and (max-width: 1024px){
.header{
    height: 68hv;
}
}

/* MEDIA QUERIES (SMALL DEVICES) */
@media screen and (max-width: 1024px){
.header{
    height:100hv;
}
.header_socials,.scroll_down{
    display: none;
}
}
nav{
    text-align: center;
    background:rgba(0, 0, 0, 0.3);
    width: 10;
    display: block;
    padding: 1.5rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom:2rem;
    display: flex;
    grid-gap:2rem;
    gap:2rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px);
}

nav a:hover{
    opacity:0.8;
    background-color: rgb(0, 0, 0);
    padding:1rem;
    border-radius: 100%;
    display: flex;
    color:var(--color-light);
    font-size: 1rem;
    padding: 1;
}




.headers{
    text-align: center;
}

.headers > h2{
  color: var(--color-primary);
}

.experience_container{
    
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap:2rem;
    gap:2rem;
    align-items: center;
    padding: 10%;
}

.experience_details{
  text-align: center;
  
}


.experience_container > div{
    background: var(--color-bg-variant);
    padding: 10%;
    border-radius: 2rem;
    border: 1px solid transparent;
    position: relative;
    left: 30% ;
    text-align: center;
    margin-top: 0;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    
    
}


.experience_content:hover{
    background-color:#242483;
    border-color: var(--color-white);
    transition: var(--transition);
}

.experience_container > div h4{
    text-align: center;
    margin-bottom: 2rem;
    color:var(--color-primary);
}



.skills_image{
    width: 50%;
    position: relative;
    left: 40%;
    overflow:hidden;
    -webkit-transform:rotate(0deg);
            transform:rotate(0deg);
    transition: var(--transition);
}

.skills_image:hover{
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
    -webkit-transform-origin: center;
            transform-origin: center;
    transition: var(--transition);
}



/* MEDIA QUERIES (medium devices) */

@media screen and (max-width: 1024px){
 .experience_container{
    grid-template-columns: 1fr;
    margin-top: 20%;
    width: var(--container-width-md);
 }

 
 .experience_container > div{
    display:inline-flex;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding:1rem;
    grid-gap:0px;
    gap:0px;
    left:0%;
 }
  .skills_image {
    width: 40%;
    position: relative;
    top: 10%;
  }

  .skills_image:hover{
    -webkit-transform:rotate(360deg);
            transform:rotate(360deg);
    transition: var(--transition);
  }
}

/* MEDIA QUERIES (small devices)*/
 @media screen and (max-width: 600px){
  .experience_container{
    grid-gap: 1.5rem;
    gap: 1.5rem;
    width: var(--container-width-sm);
  }
  
} 


.portofolio_container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap:2.5rem;
    gap:2.5rem;
}


.portofolio_items{
    background: var(--color-bg-variant);
    padding: 1,3rem;
    border-radius: 0.5rem;
    border: 1px solid transparent;
    transition: var(--transition);
    grid-gap: 1rem;
    gap: 1rem;
}

/* .fix{
    position: relative;
    left:110%;
    
} */

.portofolio_items:hover{
    border-color: var(--color-bg-variant);
    background: transparent;
    border-color: aliceblue;
}

.portofolio_item_img{
    border-radius: 0.5rem;
    border-color: var(--color-primary-variant);
    background: transparent;
    overflow: hidden;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    margin: auto;
   
    
}

.portofolio_items h3{
    margin: 1rem;
   
}

.btn{
    display: flex;
    margin: 5px;
    
}


/* MEDIUM DEVICES */

@media screen  and (max-width: 1024px){
    .portofolio_container{
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
  
}


/* SMALL DEVICES */
@media screen  and (max-width: 600px){
    .portofolio_container{
        width:var(--container-width-sm)
}
}
.contact_container{
    width: 58%;
    display: grid;
    grid-template-columns:30% 58%;
    grid-gap:12%;
    gap:12%;
}

.contact_options{
    display: flex;
    flex-direction:column;
    grid-gap:1.2rem;
    gap:1.2rem;
}

.contact_option{
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius:1rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}

.contact_option:hover{
    background: transparent;
    border-color:var(--color-primary-variant);
}

.icons{
   font-size: 1.5rem; 
   margin-bottom: 0.5rem;
}

.icons a{
  margin-top:0.7rem ;
  display: inline-block;
  font-size: 0.8rem;
  color: var(---color-light);
}

/* FORM STYLING */

form{
    display: flex;
    flex-direction:column;
    grid-gap:1.2rem;
    gap:1.2rem;
}

input,textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color:var(--color--white);
    font-weight: bold;
}


/* MEDIA QUERIES (medium devices)*/

@media screen and (max-width: 1024px){
  .container.contact_container{
    margin-top: 180%;
    grid-template-columns: 1fr;
    grid-gap:2rem;
    gap:2rem;
  }
}

/* MEDIA QUERIES (small devices)*/

@media screen and (max-width: 600px){
    .container.contact_container{
        width:var(--container-width-sm) ;
      }
}




